.App-sidebar {
    display: -ms-flexbox;
    display: flex;
    min-width: 150px;
    height: 100%;
    background: #222;
    padding: 0 10px;
}

.App-sidebar-nav a {
    width: 100%;
    display: block;
    padding: 10px 10px;
    cursor: pointer;
    text-decoration: none;
    color: #fff;
}


